<template>
  <div class="m-header">
    <div class="icon" @click="showDetail"></div>
    <h1 class="text" @click="showDetail">Yu-Music</h1>
    <transition name="fade">
      <div v-show="detailShow" class="detail">
        <div class="detail-wrapper clearfix">
          <div class="detail-main">
            <h1 class="desc"> Vue  (2.x)  WebApp AppUI</h1>
            <h1 class="bold"></h1>
            <ul class="desc-item">
              <li>Vue MVVM </li>
              <li>vue-router Lazy Loading Routes </li>
              <li>vuexVue </li>
              <li>vue-lazyload</li>
              <li>better-scrolliscroll </li>
              <li>styluscss </li>
              <li>ES6ECMAScript PromiseClass </li>
            </ul>
            <img class="vue-music" src="./vue-music.jpg" alt="">
            <p class="buy-desc">()</p>
          </div>
        </div>
        <div class="detail-close" @click="hideDetail">
          <i class="icon-close"></i>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        detailShow: false
      }
    },
    methods: {
      showDetail() {
        this.detailShow = true
      },
      hideDetail() {
        this.detailShow = false
      }
    }
  }
</script>

<style scoped lang="stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .m-header
    position: relative
    height: 44px
    text-align: center
    color: $color-theme
    font-size: 0
    .icon
      display: inline-block
      vertical-align: top
      margin-top: 6px
      width: 32px
      height: 32px
      border-radius: 50%
      margin-right: 9px
      bg-image('logo')
      background-size: 30px 32px
    .text
      display: inline-block
      vertical-align: top
      line-height: 44px
      font-size: $font-size-large
    .detail
      position: fixed
      z-index: 1000
      top: 0
      left: 0
      width: 100%
      height: 100%
      overflow: auto
      backdrop-filter: blur(10px)
      opacity: 1
      background: rgba(7, 17, 27, 0.8)
      &.fade-enter-active, &.fade-leave-active
        transition: all 0.5s
      &.fade-enter, &.fade-leave-active
        opacity: 0
        background: rgba(7, 17, 27, 0)
      .detail-wrapper
        width: 100%
        min-height: 100%
        .detail-main
          padding-top: 32px
          padding-bottom: 100px
          .desc
            line-height: 16px
            text-align: center
            font-size: 16px
            font-weight: 700
          .bold
            font-size: 24px
            color: #fff
            text-align: left
            padding: 10px 3%
          .desc-item
            font-size: 14px
            color: #f69
            text-align: left
            li
              padding: 5px 3%
          .vue-music
            width: 50%
            margin: 5px auto  
          .buy-desc
            font-size: 12px
            color: #fff
      .detail-close
        position: relative
        width: 32px
        height: 32px
        margin: -64px auto 0 auto
        clear: both
        font-size: 32px
</style>


